
import Divider from 'vux/src/components/divider/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: '',
directives: {
TransferDom
},
components: {
Divider,
Icon,
Cell,
XNumber,
Group,
Toast,
Confirm
},
destroyed () { // 销毁时显示
this.$store.commit('components', [true, true, true])
console.log('destroyed', this.$store)

},
mounted () { // 隐藏
console.log('mounted', this.$store)
this.$store.commit('components', [true, false, true])

},
data () {
return {
confirmShow: false,
len: 1,
toastData: {
isShow: false,
type: 'warn',
text: '已成功删除'
},
checked: true,
nofreight: '',
lists: [],
delId: ''
}
},
computed: {
amouts () { // 金额计算
let s = 0
this.lists.map((item) => {
if (item.checked) {
s += Number(item.proamount) * Number(item.number)
}
})
return s
}
},
async created () {
this.nofreight = sessionStorage.getItem('nofreight')
this.getLists()
},
methods: {
checkAll () { // 全选
this.checked = !this.checked
this.lists.map((item, index) => {
item.checked = this.checked
})

},
check (item, index) { // 单选
this.lists.some((item1, index1) => {
if (index === index1) {
item1.checked = !item1.checked
return true
}
})
let bool = this.lists.every((item1, index1) => { // 全选
return item1.checked
})
this.checked = bool

},
async getLists () {
let result = await this.axios.post('cart/cartlist')
if (result.success) {
result.data.map((item, index) => {
item.checked = true
item.img = this.base_img + item.propic
})
this.lists = result.data
this.len = result.data.length

}
},
/* accounts (lists) { // 计算总额 proamount * number
this.amouts = 0
lists.map((item) => {
if (item.checked) {
this.amouts += Number(item.proamount) * Number(item.number)
}
})
}, */
settle () { // 结算
let settleLists = [] 
this.lists.map((item, index) => {
if (item.checked) {
settleLists.push(item)
}
})
if (settleLists.length > 0) {
settleLists = JSON.stringify(settleLists)
console.log(settleLists)
sessionStorage.setItem('settleLists', settleLists)
this.$router.push({name: 'Settle', params: {type: 3}})
} else {
this.toastData = {
isShow: true,
type: 'warn',
text: '请选择商品'
}
}
},
del (id) { // 删除
this.confirmShow = true
this.delId = id
},
async onConfirm () {
let _this = this
let result = await this.axios.get('cart/cartdel/' + this.delId)
if (result.success) {
this.toastData = {
isShow: true,
type: 'success',
text: '已成功删除'
}
_this.lists.some((item, index) => {
if (item.id === _this.delId) {
_this.lists.splice(index, 1)
}
})

} else {
this.toastData = {
isShow: true,
type: 'warn',
text: '删除失败'
}
}
},
async addNum (id) { // 加
this.lists.some((item, index) => {
if (item.id === id) {
item.number++
return true
}
})
let result = await this.axios.get('cart/cartnumberadd/' + id)
if (result.success) {
}
},
async reduceNum (id) { // 减
let bool = true
this.lists.some((item, index) => {
console.log('item.id', item.id)
console.log('id', id)
console.log('item.number', item.number)
if (item.id === id && item.number > 1) { // 最少一个
item.number--
return true
} else if (item.id === id && item.number <= 1) {
bool = false
this.toastData = {
isShow: true,
type: 'warn',
text: '不能再少了'
}
return false
}
})
if (bool) {
let result = await this.axios.get('cart/cartnumbersub/' + id)
if (result.success) {
}
}
}
}
}



